import { Component, Input, ViewChild } from '@angular/core';

import { HostListener, Inject } from "@angular/core";
import { data } from 'jquery';
import { HeaderComponent } from './header/header.component';
import { HomeComponent } from './home/home.component';
import {DataService} from './data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
   title = 'INaleva';
   display:string;
  subscription:any;

   get data():string{
     return this.dataService.sharedData;
   }

   set data(value:string){
    this.dataService.sharedData = this.display;
  }

  constructor(private dataService:DataService){
    this.subscription = dataService.stringVar$.subscribe(data => {
      this.display = dataService.sharedData;

});
  }

   ngAfterViewInit() {
    this.display = this.dataService.sharedData;

   }

  }

