import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-whatsapp',
  templateUrl: './whatsapp.component.html',
  styleUrls: ['./whatsapp.component.css']
})
export class WhatsappComponent implements OnInit {

  constructor() { }
  ngOnInit() {
    var input = document.querySelector("#numberInput");
    (<any>window).intlTelInput(input, {
        utilsScript: 'https://cdnjs.cloudflare.com/ajax/libs/intl-tel-input/17.0.5/js/utils.js',
        initialCountry: 'il'

    });
  }

  openChat(inputNumber: Number){
    window.open('https://wa.me/972'+inputNumber);
  }
}
  

